.container {
  width: 100%;
  padding: 5% 10% 0 10%;
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
  max-height: 500px;
  height: 80vh;
  align-items: center;

  img {
    object-fit: cover;
    height: 100%;
    width: auto;
  }

  article {
    margin-top: -10%;
    max-width: 550px;
  }

  h1 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 55px;
    text-decoration: underline;
    color: var(--orange);
    margin-bottom: 20px;
  }

  p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 2rem;
    line-height: 130%;
    letter-spacing: 0.05em;
    color: var(--gray);
  }
}

@media only screen and (max-width: 1024px) {
  .container {
    grid-template-columns: 100%;
    grid-template-rows: 50% 50%;
    border-radius: 2rem;
    box-shadow: 0px 4px 10px 2px rgba(0, 0, 0, 0.25);
    padding: 0;
    margin: 2rem 0;

    article {
      padding: 0 1rem;
    }

    img {
      grid-row: 2;
    }

    h1 {
      font-size: 2.8rem;
      text-align: center;
    }

    p {
      text-align: center;
    }
  }
}
