.container {
  width: 100%;
}

.container button {
  left: 10vw;
}

.container button:last-child {
  left: 90vw;
}

.container button:last-child::before {
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23ff8f29' class='bi bi-chevron-right' viewBox='0 0 16 16'><path fill-rule='evenodd' d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'/></svg>");
}

.container button::before {
  width: 4rem;
  height: 4rem;
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23ff8f29' class='bi bi-chevron-right' viewBox='0 0 16 16'><path fill-rule='evenodd' d='M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z'/></svg>");
  background-size: cover;
}

@media only screen and (max-width: 1024px) {
  .container {
    margin: 3rem 0;
  }
  .container .carousel-track {
    margin: 3rem 0;
  }
}
